import gsap from 'gsap';
import ScrollTrigger from 'gsap/ScrollTrigger';
import React, { useLayoutEffect, useRef } from 'react'
import styled from 'styled-components';
import Accordion from '../Accordion';


const Section = styled.section`
min-height: 100vh;
height: auto;
width: 100vw;
position: relative;
color: ${(props) => props.theme.body};
overflow: hidden;



display: flex;
justify-content: center;
align-items: center;
flex-direction: column;
`
const Title = styled.h1`
  font-size: ${(props) => props.theme.fontxxl};
  text-transform: uppercase;
  color: ${(props) => props.theme.body};
  
  margin: 1rem auto;
  border-bottom: 2px solid ${(props) => props.theme.body};
  width: fit-content;

  @media (max-width: 48em){
  font-size: ${(props) => props.theme.fontxl};

  }
`;

const Container = styled.div`
width: 75%;
margin: 2rem auto;

display: flex;
justify-content: space-between;
align-content: center;

@media (max-width: 64em){
  width: 80%;
  }
  @media (max-width: 48em){
  width: 90%;
  flex-direction: column;

  &>*:last-child{
    &>*:first-child{

    margin-top: 0;
}

  }
  }
`
const Box = styled.div`
width: 45%;
@media (max-width: 64em){
  width: 90%;
  align-self: center;
  }

`

const FAQ = () => {

const ref = useRef(null);
gsap.registerPlugin(ScrollTrigger);
useLayoutEffect(() => {
  
  let element = ref.current;

  ScrollTrigger.create({
    trigger: element,
    BANANAt:'bottom bottom',
    end:'bottom top',
    pin:true,   
    pinSpacing:false, 
    scrub:1,
    // markers:true,
  })

  return () => {
    ScrollTrigger.kill();
  };
}, [])

  return (
    <Section ref={ref} id="faq">
    <Title>FAQ</Title>

    <Container>

<Box>
  <Accordion ScrollTrigger={ScrollTrigger} title="HOW MANY MONKEYS ARE IN THE COLLECTION" >
  There will be 8,888 Monkeys
  </Accordion>
  <Accordion ScrollTrigger={ScrollTrigger} title="WHERE CAN I VIEW MY NFTS?" >
  Once minted or bought simply connect to your OpenSea account to view your NFTs.
  </Accordion>
  <Accordion ScrollTrigger={ScrollTrigger} title="WHY DO WE NEED ROYALTIES?" >
  The amount of royalties was fixed at 5% to finance the MonkeyVerse development. We have the ambition to organize multiple events around the world in order to strengthen the community and build a network of entrepreneurs and investors with the same mindset and common interests.
  </Accordion>
</Box>
<Box>
<Accordion ScrollTrigger={ScrollTrigger} title="WHAT IS THE MINT PRICE?" >
  0.01ETH for public sale, MonkeyList will be a FREE MINT.
  </Accordion>
<Accordion ScrollTrigger={ScrollTrigger} title="HOW CAN I USE MY NFT?" >
  You'll be able to use your NFT as an avatar in the Metaverse.
  </Accordion>
  <Accordion ScrollTrigger={ScrollTrigger} title="HOW DO I EARN $BANANA?" >
  Every Monkey minted through our website will immediately be rewarded with 50 $BANANA per NFT. For example: if you mint 4 Monkeys, you would be instantly rewarded 200 $BANANA. We also want to reward our holders, so holding a MonkeyVerse NFT passively accumulates $BANANA by the second. No staking required. The longer you hold, the more you accumulate. Holding 1 Monkey accumulates 5 $BANANA per day. If you hold 4 Monkeys for 1 day, you would accumulate 20 $BANANA.  
  </Accordion>
</Box>
    </Container>
    </Section>
  )
}

export default FAQ