import React, { lazy, Suspense } from 'react'
import styled, { ThemeProvider } from 'styled-components'
// import Carousel from '../Carousel'
import Button from '../Button';
import {light} from '../../styles/Themes';
import Loading from '../Loading';


const Carousel = lazy(() => import("../Carousel"));

const Section = styled.section`
height: fit-content;
width: 100%;
display: flex;
justify-content: center;
align-items: center;
position: relative;
overflow: hidden;

`
const Container = styled.div`
width: 75%;
margin: 0 auto;
/* background-color: lightblue; */

display: flex;
justify-content: center;
align-items: center;
@media (max-width: 70em){
  width: 85%;
}

@media (max-width: 64em){
  width: 100%;
  flex-direction: column;

  &>*:last-child{
    width: 80%;
  }
}
@media (max-width: 40em){
  margin: 20rem auto;
  &>*:last-child{
    width: 90%;
  }
}
`
const Box = styled.div`
width: 50%;
height: 100%;
min-height: 60vh;
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;

@media (max-width: 40em){
  min-height: 50vh;
}
`
 
const Title = styled.h2`
  font-size: ${(props) => props.theme.fontxxl};
  text-transform: capitalize;
  color: ${(props) => props.theme.body};
  align-self: flex-start;
  width: 80%;
  margin: 0 auto;

@media (max-width: 64em){
  width: 100%;
  text-align:center;
}
@media (max-width: 40em){
  font-size: ${(props) => props.theme.fontxl};

}
@media (max-width: 30em){
  font-size: ${(props) => props.theme.fontlg};

}
`
const SubText = styled.p`
  font-size: ${(props) => props.theme.fontlg};
  color: ${(props) => props.theme.body};
  align-self: flex-start;
  width: 80%;
  margin: 1rem auto;
  font-weight:400;
  @media (max-width: 64em){
  width: 100%;
  text-align:center;
  font-size: ${(props) => props.theme.fontmd};

}
@media (max-width: 40em){
  font-size: ${(props) => props.theme.fontmd};

}
@media (max-width: 30em){
  font-size: ${(props) => props.theme.fontsm};

}

`
const SubTextLight = styled.p`
  font-size: ${(props) => props.theme.fontmd};
  color: ${(props) => `rgba(${props.theme.bodyRgba},0.8)`};
  align-self: flex-start;
  width: 80%;
  margin: 1rem auto;
  font-weight:400;

  @media (max-width: 64em){
  width: 100%;
  text-align:center;
  font-size: ${(props) => props.theme.fontsm};

  }
  @media (max-width: 40em){
  font-size: ${(props) => props.theme.fontsm};

  }
  @media (max-width: 30em){
  font-size: ${(props) => props.theme.fontxs};

  }
`
const ButtonContainer = styled.div`
 width: 80%;
 display: flex;
 justify-content: center;
 align-self: flex-start;
 

  @media (max-width: 64em){
    width: 80%;
    justify-content: center;

    Button{
      margin: 0 auto;
  
}
}

`

const About = () => {
    return (
        <Section id="about">
            <Container>
            <Box> 
                <Suspense fallback={<Loading />}>
                    <Carousel /> </Suspense>
            </Box>
                <Box> 
                    <Title>
                        Welcome to MonkeyVerse
                    </Title>
                    <SubText>
                        MonkeyVerse is a P2E ecosystem involving quests, raids and dungeons, PVP will also be available in some areas of the game. 
                    </SubText>
                    <SubTextLight>

                    </SubTextLight>
                <ButtonContainer >
                    <ThemeProvider theme={light}>
                    <a href="https://discord.com/invite/bwzW76785r">
                        <Button text="JOIN OUR DISCORD" />
                    </a>
                    </ThemeProvider>
                </ButtonContainer>
                </Box>
            </Container>
        </Section>
    )
}

export default About