import React, { useRef } from "react";
import styled, { keyframes } from "styled-components";

import img1 from '../../assets/Monkey/0001.png';
import img2 from '../../assets/Monkey/0002.png';
import img3 from '../../assets/Monkey/0003.png';
import img4 from '../../assets/Monkey/0004.png';
import img5 from '../../assets/Monkey/0005.png';
import img6 from '../../assets/Monkey/0006.png';
import img7 from '../../assets/Monkey/0007.png';
import img8 from '../../assets/Monkey/0008.png';
import img9 from '../../assets/Monkey/0009.png';
import img10 from '../../assets/Monkey/0010.png';
// import ETH from '../../assets/Ethereum.svg'




const Section = styled.section`
min-height: 100vh;
width: 100vw;
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
position: relative;
overflow: hidden;

&>*:first-child{
  animation-duration: 20s;

  @media (max-width: 30em){
    animation-duration: 15s;

  }
}
&>*:last-child{
  animation-duration: 15s;
  @media (max-width: 30em){
    animation-duration: 10s;

  }
}
`
const move = keyframes`
0%{ transform: translateX(100%)   };
100%{ transform: translateX(-100%)   }

`

const Row = styled.div`
/* background-color: lightblue; */
white-space: nowrap;
box-sizing:content-box;
margin: 2rem 0;
display: flex;

animation: ${move}  linear infinite ${props => props.direction};


`
const ImgContainer = styled.div`
width: 15rem;
margin: 0 1rem;

border-radius: 20px;
cursor: pointer;

@media (max-width: 48em){
  width: 12rem;
  }
  @media (max-width: 30em){
  width: 10rem;
  }

img{
  width: 100%;
  height: auto;
}
`





const NftItem = ({img, passRef}) => {

    let play = (e) => {
      passRef.current.style.animationPlayState = 'running';
    }
    let pause = (e) => {
      passRef.current.style.animationPlayState = 'paused';
    }
    
    
      return(
        <ImgContainer   onMouseOver={e => pause(e) }  onMouseOut={e => play(e) }  >
          <img width={500} height={400}  src={img} alt="MonkeyVerse" />
          </ImgContainer>
      )
}



const Showcase = () => {

    const Row1Ref = useRef(null);
    const Row2Ref = useRef(null);

    return (
        <Section id="showcase">
            <Row direction='none' ref={Row1Ref}>
                <NftItem img={img1}   passRef = {Row1Ref} />
                <NftItem img={img2}   passRef = {Row1Ref} />
                <NftItem img={img3}   passRef = {Row1Ref} />
                <NftItem img={img4}   passRef = {Row1Ref} />
                <NftItem img={img5}   passRef = {Row1Ref} />
                <NftItem img={img6}   passRef = {Row1Ref} />
                <NftItem img={img7}   passRef = {Row1Ref} />
                <NftItem img={img8}   passRef = {Row1Ref} />
                <NftItem img={img9}   passRef = {Row1Ref} />
                <NftItem img={img10}  passRef = {Row1Ref} />
            </Row>
            <Row direction='reverse' ref={Row2Ref}>
                <NftItem img={img1}    passRef = {Row2Ref} />
                <NftItem img={img2}    passRef = {Row2Ref} />
                <NftItem img={img3}    passRef = {Row2Ref} />
                <NftItem img={img4}    passRef = {Row2Ref} />
                <NftItem img={img5}    passRef = {Row2Ref} />
                <NftItem img={img6}    passRef = {Row2Ref} />
                <NftItem img={img7}    passRef = {Row2Ref} />
                <NftItem img={img8}    passRef = {Row2Ref} />
                <NftItem img={img9}    passRef = {Row2Ref} />
                <NftItem img={img10}   passRef = {Row2Ref} />
            </Row>
        </Section>
    )
}

export default Showcase