const {MerkleTree} =  require('merkletreejs');
const keccak256 = require('keccak256');

const monkeylist = require ('../monkeylist');

const monkeyVerseAddress = '0xD32f1A523e76a7BBD14360a5B931F31a5A897f6A';


const monkeyList = monkeylist.monkeylistAddresses();
let leafNodes = monkeyList.map(addr => keccak256(addr));
let merkleTree = new MerkleTree(leafNodes, keccak256, {sortPairs: true});
const rootHash = merkleTree.getHexRoot();
console.log(rootHash,'roothash');
 



function Mint ({ accounts, setAccounts }) {
    

    const [mintAmount, setMintAmount] = useState(1);
    const isConnected = Boolean(accounts[0]);
    
   
    
    async function handlePresaleMint() {
        if (window.ethereum) {
            const provider = new ethers.providers.Web3Provider(window.ethereum);
            const signer = provider.getSigner();
            const address = (await signer.getAddress()).toString();
            console.log(address)
            let index = monkeyList.indexOf(address);
            console.log(index)
            const contract = new ethers.Contract(
                monkeyVerseAddress,
                monkeyverse.abi,
                signer
            );
            if (index === -1) {
                alert('You must be allowlisted to mint these Monkeys!!!');
                handlePublicMint();
            } else {
                try {
                    let clamingAddress = leafNodes[index];
                    let hexProof = merkleTree.getHexProof(clamingAddress);
                    console.log(hexProof , 'hexProof')
                    const response = await contract.presaleMint(BigNumber.from(mintAmount), hexProof);
                    console.log('response: ', response);
                    
                } 
                catch (err) {
                    console.log('error', err )
                    
                }
            }
            
        }
    }

   

    async function handlePublicMint() {
        if (window.ethereum) {
            const provider = new ethers.providers.Web3Provider(window.ethereum);
            const signer = provider.getSigner();
            const contract = new ethers.Contract(
                monkeyVerseAddress,
                monkeyverse.abi,
                signer
            );
            try {
                const response = await contract.mint(BigNumber.from(mintAmount), {value: ethers.utils.parseEther((0.01 * mintAmount).toString())});
                console.log('response: ', response)
            } 
            catch (err) {
                console.log('error', err );
            }
        }
    }

    

    const handleDecrement = () => {
        if (mintAmount <= 1 ) return;
        setMintAmount(mintAmount - 1);
    };

    const handleIncrement = () => {
        if (mintAmount >= 4 ) return;
        setMintAmount(mintAmount + 1);
    };
    

    return (
        <Section id="mint">
                
                {isConnected ? (
                <MintContainer>
                
                        <div>
                            <MintItem>
                                <Button onClick={handleDecrement}>
                                    -
                                </Button>

                                <Input readOnly type='number'>
                                        {mintAmount}
                                </Input>

                                <Button1 onClick={handleIncrement}>
                                    +
                                </Button1>
                            </MintItem>
                        </div>
                            <MintButtons>
                                <MintButton onClick={handlePresaleMint}>
                                      Monkeylist Mint
                                </MintButton>
                            
                                <PublicMintButton onClick={handlePublicMint}>
                                      Mint
                                </PublicMintButton>
                             </MintButtons>
                             </MintContainer>): (
                                <Pic>
                                    <BananaContainer>
                                     <img src={Banana} />
                                     Connect your wallet at the top!
                                    </BananaContainer>
                                </Pic>
                    
                             )}
        </Section>
    );
}

export default Mint;
