import React from "react";
import styled from "styled-components";
import Logo from "./Logo";
import { useState } from "react";
import '../../src/Connect.css';
import ETHlogo from "../assets/Ethereum.svg";



const Section = styled.section`
width: 100vw;
`;
const NavBar = styled.nav`
display: flex;
justify-content: space-between;
align-items: center; 


width: 90%;
height: ${props => props.theme.navHeight};
margin: 0 auto;

`;

const Menu = styled.ul`
display: flex;
justify-content: space-between;
align-items: center;
list-style: none;


@media (max-width: 1024px) {
        position: fixed;
        top: ${props => props.theme.navHeight};
        left: 0;
        right: 0;
        bottom: 0;
        width: 100vw;
        height: ${props => `calc(100vh - ${props.theme.navHeight})`};
        z-index: 50;
        background-color: ${props => `rbga(${props.theme.bodyRbga},0.85)`};
        backdrop-filter: blur(3px);

        transform: ${props => props.click ? 'translateY(0)' : 'translateY(1000%)'};
        transition: all 0.3s ease;
    
        flex-direction: column;
        justify-content: center;
}
`;
const MenuItem = styled.li`
margin: 0 1rem;
color: ${props => props.theme.body};
cursor: pointer;

&::after{
    content: ' ';
    display: block;
    width: 0%;
    height: 2px;
    background: ${props => props.theme.body};
    transition: width 0.3s ease;
}
&:hover::after{
    width: 100%;
}

@media (max-width: 1024px) {
    margin: 1rem 0;

    &:hover{
        transform: scale(0.9);
    }
    transition: width 0.3s ease;
    &::after{
        display: none;
    }
}
`;
const MenuItem2 = styled.li`
margin: 0 1rem;
color: ${props => props.theme.body};
cursor: pointer;

@media (max-width: 1024px) {
    margin: 1rem 0;

    &:hover{
        transform: scale(0.9);
    }
    transition: width 0.3s ease;
    &::after{
        display: none;
    }
}
`;
const Button = styled.button`
display: inline-block;
background-color: ${props => props.theme.body};
color: ${props => props.theme.text};
outline: none;
border: none;

font-size: ${props => props.theme.fontsm};
padding: 0.5rem 1rem;
border-radius: 50px;
cursor: pointer;
transiton: all 0.2s ease;
position: relative;


&:hover{
    transform: scale(0.9);
}
&::after{
    content: ' ';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(0);
    border: 2px solid ${props => props.theme.body};
    width: 100%;
    height: 100%;
    border-radius: 50px;
    transiton: all 0.2s ease;
}
&:hover::after{
    width: 100%;
}


@media (max-width: 1024px) {
    display: none;
}

`;
const Button1 = styled.button`
display: inline-block;
background-color: ${props => props.theme.body};
color: ${props => props.theme.text};
outline: none;
border: none;

font-size: ${props => props.theme.fontsm};
padding: 0.5rem 1rem;
border-radius: 50px;
cursor: pointer;
transiton: all 0.2s ease;
position: relative;


&:hover{
    transform: scale(0.9);
}
&::after{
    content: ' ';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(0);
    border: 2px solid ${props => props.theme.body};
    width: 100%;
    height: 100%;
    border-radius: 50px;
    transiton: all 0.2s ease;
}
&:hover::after{
    width: 100%;
}

display: none;

@media (max-width: 1024px) {
    display: inline-block;
}
`;
const HamburgerMenu = styled.span`
width: ${props => props.click ? '2rem' : '1.5rem'};
height: 2px;
background: ${props => props.theme.body};

position: absolute;
top: 2rem;
left: 50%;
transform: ${props => props.click ? 'translate(-50%) rotate(90deg)' : 'translate(-50%) rotate(0)'};

display: none;
justify-content: center;
align-items: center;

cursor: pointer;
transition: all 0.3s ease;


@media (max-width: 1024px) {
    display: flex;
    justify-content: center;
    align-items: center;
}
@media (max-width: 550px) {
    margin: 0 8rem;
    justify-content: center;
    align-items: center;
}

&::after, &::before{
    content: ' ';
    width: ${props => props.click ? '1rem' : '1.5rem'};
    height: 2px;
    right: ${props => props.click ? '-2px' : '0'};
    background: ${props => props.theme.body};
    position: absolute;

    transition: all 0.3s ease;
}
&::after{
    top: ${props => props.click ? '0.3rem' : '0.5rem'};
    transform: ${props => props.click ? 'rotate(-40deg)' : 'rotate(0)'};
}
&::before{
    bottom: ${props => props.click ? '0.3rem' : '0.5rem'};
    transform: ${props => props.click ? 'rotate(40deg)' : 'rotate(0)'};
}`;



const Navigation =({ accounts, setAccounts }) => {

    const isConnected = Boolean(accounts[0]);
    const [accountAddress, setAccountAddress] = useState('');

    


    async function connectAccount() {
        if (window.ethereum) {
            const accounts = await window.ethereum.request({
                method: 'eth_requestAccounts',
            });
            setAccounts(accounts);
            setAccountAddress(accounts[0]);
        }
    }
    

    


    const [click, setClick] = useState(false);

    const scrollTo = (id) => {

        let element = document.getElementById(id);

        element.scrollIntoView({
            behavior: 'smooth',
            block: 'start',
            inline: 'nearest'
        })
        setClick(!click);
    }


    return (
        <Section id="navigation">
        <NavBar>
            <Logo />
            <HamburgerMenu click={click} onClick={() => setClick(!click)}>
                &nbsp;
            </HamburgerMenu>
            <Menu  click={click}>
                <MenuItem  onClick={() => scrollTo('home')}  >Home</MenuItem>
                <MenuItem  onClick={() => scrollTo('mint')}  >Mint</MenuItem>
                <MenuItem  onClick={() => scrollTo('about')}  >About</MenuItem>
                <MenuItem  onClick={() => scrollTo('roadmap')}  >Roadmap</MenuItem>
                <MenuItem  onClick={() => scrollTo('showcase')}  >Showcase</MenuItem>
                <MenuItem  onClick={() => scrollTo('team')}  >Team</MenuItem>
                <MenuItem  onClick={() => scrollTo('faq')}  >FAQ</MenuItem>
                <MenuItem>
                {isConnected ? (
                        <Button1> 
                            {accountAddress.slice(0, 4)}...
                            {accountAddress.slice(38, 42)}
                        </Button1>
                    ) : (
                        <Button1 onClick={connectAccount} className="mobile">
                            Connect <img src={ETHlogo} className="App-logo" alt="logo" />
                        </Button1>
                    )}
                </MenuItem>
                
                <MenuItem2>
                    {/* connect */}
                    {isConnected ? (
                        <Button> 
                            {accountAddress.slice(0, 4)}...
                            {accountAddress.slice(38, 42)}
                        </Button>
                    ) : (
                        <Button onClick={connectAccount} className="desktop">
                            Connect <img src={ETHlogo} className="App-logo" alt="logo" />
                        </Button>
                    )}
                </MenuItem2>
            

            </Menu>
          
        </NavBar>
        
        </Section>
    )
}

export default Navigation